footer{
    position: relative;
    height: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    min-height: 40vh;
    background: #262626;
    overflow: hidden;
    color: #fff;
    padding: 2rem 1rem;
    font-family: 'Lusitana', serif;
    font-weight: bold;
    text-align: center;
    font-size: 14px;
}

footer h1{
    margin-top: 65px;
    margin-bottom: 75px;
    letter-spacing: 10px;
    text-transform: uppercase;
}

#footer-icon{
    position: absolute;
    transform: translate(-50%, -50%);
    left: 50%;
    top: 70%;
    z-index: 999;
}

footer p{
    letter-spacing: 3px;
    max-width: 700px;
}

.social-links-footer{
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 0;
    margin: 0;
    margin-top: 50px;
    list-style: none;

}
.social-links-footer li{
    width: 45px;
    height: 45px;
    margin: 0 15px;
    @media screen and (max-width: 768px){
        width: 35px;
        height: 35px;
    }
}

.social-links-footer li:last-child{
    margin-top: 7px;
}
